import React, { useState, useEffect } from "react";
import { Navbar, Nav, Container, Button } from "react-bootstrap";
import { FaSignInAlt, FaUserPlus } from "react-icons/fa";
import { Link, useNavigate, useLocation } from "react-router-dom";
import "../pages/landingNavbar.css";
import transparentLogo from "../../../asesets/banner/bankmudraa_logonew.png";

const Header = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const menus = [
    { key: "home", label: "Home", path: "/" },
    { key: "Services", label: "Services", path: "/services" },
    { key: "disclaimer", label: "Disclaimer", path: "/disclaimer" },
    { key: "contact", label: "Contact Us", path: "/contact-us" },
    { key: "about", label: "About Us", path: "/about-us" },
  ];

  const isActive = (path) => {
    if (path === "/") return location.pathname === "/";
    return location.pathname.startsWith(path);
  };

  return (
    <Navbar expand="lg" fixed="top" className="main-navbar" data-mobile-nav>
      <Container fluid className="px-3 px-lg-4">

        {/* LOGO */}
        <Navbar.Brand as={Link} to="/">
          <img src={transparentLogo} alt="Bank Mudra" className="logo-img" />
        </Navbar.Brand>

        <Navbar.Toggle aria-controls="navbarResponsive" />

        <Navbar.Collapse id="navbarResponsive">
          <div className="ms-lg-auto d-lg-flex align-items-lg-center w-100 justify-content-lg-end">

            {/* MENU */}
            <Nav className="nav-links flex-column flex-lg-row gap-2 gap-lg-4 me-lg-4">
              {menus.map((menu) => (
                <Nav.Link
                  as={Link}
                  to={menu.path}
                  key={menu.key}
                  bsPrefix={`nav-link-custom ${isActive(menu.path) ? "active-nav" : ""}`}
                  className="px-lg-2"
                >
                  {menu.label}
                </Nav.Link>

              ))}
            </Nav>

            {/* BUTTONS */}
            <div className="auth-buttons">
              <Button
                className="nav-btn nav-btn-login"
                onClick={() => navigate("/login-area")}
              >
                <FaSignInAlt className="btn-icon" />
                <span>Login</span>
              </Button>

              <Button
                className="nav-btn nav-btn-signup"
                onClick={() => navigate("/Signup")}
              >
                <FaUserPlus className="btn-icon" />
                <span>Sign Up</span>
              </Button>
            </div>

          </div>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
};

export default Header;
